import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MenuIcon from '@material-ui/icons/Menu';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import DraftsIcon from '@material-ui/icons/Drafts';
import SendIcon from '@material-ui/icons/Send';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';
import {Link} from 'react-router-dom';




const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  appbar:{
    height:50,
  },
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
  show:{
    marginTop:50,
   
  },
  sidebar:{
    width:"20%",
    height:"100vh",
    backgroundColor: theme.palette.background.paper,
    borderRight: "3px solid green",
    position: "absolute"
   
  },
  
  nested: {
    paddingLeft: theme.spacing.unit * 4,
  },
});
class ButtonAppBar extends React.Component {
    state = {
      anchorEl: null,
      showing: false,
      openSideList: false,
    };
    
    handleClick = event => {
      this.setState({ 
        anchorEl: event.currentTarget,
      });
    };
  
    handleClose = () => {
      this.setState({ anchorEl: null });
    };

    sideLitstClick = () => {
      this.setState({
        openSideList: !this.state.openSideList,
      });
    };
  
    render() {
      const { anchorEl } = this.state;
      const { classes } = this.props;
     
      
      return (
        <div position = "relative" className={classes.root}>
          <AppBar position="absolute" className={classes.appbar}>
          <Toolbar>
          <IconButton className={classes.menuButton} onClick={() => {this.props.sideBarOpen()}} color="inherit" aria-label="Menu">
              <MenuIcon />
          </IconButton>
          

          <Typography variant="title"  className={classes.grow}>
            Title
          </Typography>
          <IconButton  aria-owns={anchorEl ? 'simple-menu' : undefined}
            aria-haspopup="true"
            onClick={this.handleClick}
            color="inherit" aria-label="Menu">
               <AccountCircle />
          </IconButton>
          <Menu
            id="simple-menu"
            anchorOrigin={{
              vertical: "bottom",
              horizontal: "right"
            }}
            getContentAnchorEl={null}
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={this.handleClose}
          >
            <MenuItem onClick={this.handleClose}>Profile</MenuItem>
            <MenuItem onClick={this.handleClose}>My account</MenuItem>
            <MenuItem onClick={this.handleClose}>Logout</MenuItem>
          </Menu>
          
          </Toolbar> 
        </AppBar>
        <div className = {classes.show} style={{ display: (this.props.sideBarShow ? 'block' : 'none')}}>
            <List className={classes.sidebar}>
               <ListItem>
                  <ListItemIcon>
                    <SendIcon />
                  </ListItemIcon>
                  <Link to="/home">
                      Home
                  </Link>
                </ListItem>
                <Link to="/colors">
                <ListItem button>
                  <ListItemIcon>
                     <DraftsIcon />
                  </ListItemIcon>
                      Colors
                 </ListItem>
                </Link>
              <ListItem button onClick={this.sideLitstClick}>
                 <ListItemIcon>
                    <InboxIcon />
                 </ListItemIcon>
                 <ListItemText inset primary="Inbox" />
                   {this.state.openSideList ? <ExpandLess /> : <ExpandMore />}
               </ListItem>
               <Collapse in={this.state.openSideList} timeout="auto" unmountOnExit>
                  <List component="div" disablePadding>
                    <ListItem button className={classes.nested}>
                       <ListItemIcon>
                          <StarBorder />
                       </ListItemIcon>
                       <ListItemText inset primary="Starred" />
                     </ListItem>
                   </List>
                </Collapse>
            </List>
        </div>
        
        </div>

      );
    }
  }

export default withStyles(styles)(ButtonAppBar);
